.search {
    position: relative;
    background-color: #F1F0F8;
    &-border {
        border-radius: 8px;
    }
    // 
    &-index {
        top: -60px;
        //  CSS , ( 991px  992  mb-lg-0 )
        .select,
        .btn {
            @media screen and(max-width: 991px) {
                width: 100%;
            }
        }
    }
    // 
    &-header {
        position: fixed;
        width: 100%;
        z-index: 2;
        padding: 8px 0;
        @media screen and(min-width: 720px) {
            padding: 16px 0;
        }
        @media screen and(min-width: 960px) {
            padding: 24px 0;
        }
        .select {
            
            @media screen and(min-width: 720px) {
                padding: 10px 8px;
            }
            @media screen and(min-width: 960px) {
                padding: 10px 16px;
            }
        }
    }
}